import * as React from "react"
import { graphql, useStaticQuery } from "gatsby"
import Page from "@lekoarts/gatsby-theme-jodie/src/components/styles"

const StylePage = () => {
  const data = useStaticQuery(graphql`
  query Styles {
     pages: allPage(sort: { fields: date, order: DESC }, filter: {slug: {in:["/fineline", "/lettering", "/neotraditional"]}}) {
      nodes {
        slug
        title
        cover {
          childImageSharp {
            fluid(quality: 95, maxWidth: 1200) {
              ...GatsbyImageSharpFluid_withWebp
            }
          }
        }
      }
    }
  }
`)
  return <Page data={data}></Page>
}

export default StylePage

