import * as React from "react"
import { graphql, useStaticQuery } from "gatsby"
import Page from "@lekoarts/gatsby-theme-jodie/src/components/styles"

const NeoTraditionalPage = () => {
  const data = useStaticQuery(graphql`
  query NeoTraditionalQuery {
     pages: allProject(sort: { fields: date, order: DESC }, filter: {type: {eq: "Neotraditional"} }) {
      nodes {
        slug
        shortTitle
        title
        cover {
          childImageSharp {
            fluid(quality: 95, maxWidth: 1200) {
              ...GatsbyImageSharpFluid_withWebp
            }
          }
        }
      }
    }
  }
`)
  return <Page data={data}></Page>
}

export default NeoTraditionalPage
